@import 'src/styles/variables';

.WhiteBox {
  background-color: white;
  height: 100%;
  display: flow-root;
  margin-bottom: $double-margin;
  border-radius: $border-radius;

  -webkit-box-shadow: 0px 0px 20px -4px rgba(153,153,153,0.2);
  -moz-box-shadow: 0px 0px 20px -4px rgba(153,153,153,0.2);
  box-shadow: 0px 0px 20px -4px rgba(153,153,153,0.2);
}



